
import Search from 'vux/src/components/search/index.vue'


export default {
name: 'searchBar',
components:{
Search
},
data() {
return {
results: [],
value: null,
autoFixed: false,
}
},
methods:{
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
onSubmit (val) {
window.alert('on submit' + val)
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
}
}
}



