
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'header',
components:{
XHeader
},
data() {
return {
isBgBlue:this.$route.meta.isBlue?true:false,
noBack:this.$route.meta.noBack?true:false,
title:this.$route.meta.title
}
},
methods:{
back(){
this.$router ? this.$router.back() : window.history.back()
},
save(){
this.$store.commit('setOperation',{operation:true})
}
},
computed:{
operation:{
get:function () {
return this.$store.getters.operation
}
}
},
watch:{
title:function (val) {
this.title = val
return val
},
operation:function (val) {
if(val){
let _this = this
setTimeout(function () {
let url = _this.$store.getters.url
let params = _this.$store.getters.params


_this.$httpPost(url,params).then((data)=>{

_this.$store.commit('setResult',{result:data})
}).catch(err=>{

_this.$store.commit('setResult',{result:err})
})
_this.$store.commit('setOperation',{operation:false})
})

}
}
}
}



