
  import AddressItem from './_AddressItem.vue'
  export default {
    name: 'address',
    components:{
      AddressItem
    },
    data() {
      return {
        addressList:[]
      }
    },
    methods:{
      getAddressList(){
        this.$httpPost('/wx/userAddress/list',{
        }).then((data)=>{
          this.addressList = data.data
        }).catch(err=>{
          this.$vux.toast.text('系统出错','middle')
        })
      },
      changeDefaultAddress(val){
//        console.log(val)
        this.addressList.forEach(function (item) {
          if(item.id!==val){
            item.defaul = false
          }else{
            item.defaul = true
          }
        })
        this.$httpPost('/wx/userAddress/setDefaultAddress',{
          id:val
        }).then((data)=>{
//          console.log(data)
          this.$vux.toast.text(data.data,'middle')
        }).catch(err=>{
          this.$vux.toast.text('系统错误','middle')
        })
      }
    },
    created(){
      this.getAddressList()
    }
  }



