import store from '../../vuex'
import http from '../../config/http'

/**
 * 时间戳转换为时间
 * @returns {date}
 */
export function timetrans(date){
  var date = new Date(date);//如果date为10位不需要乘1000
  var Y = date.getFullYear() + '-';
  var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
  var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
  var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
  var m = (date.getMinutes() <10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
  var s = (date.getSeconds() <10 ? '0' + date.getSeconds() : date.getSeconds());
  return Y+M+D+h+m+s;
}

/**
 * 当前是否登陆
 * @returns {boolean}
 */
export function isLogin() {
  let token = store.getters.token;
  if (!token) {
    alert('登录之后才能继续操作哦！');
    store.dispatch('updateLoginState', true);
    return false
  }
  return true
}

/**
 * 获取页面可视高度
 * @returns {[*,*]}
 */
export function getWinSize() {
  if (window.innerWidth !== undefined) {
    return [window.innerWidth, window.innerHeight];
  }
  else {
    let B = document.body,
      D = document.documentElement;
    return [Math.max(D.clientWidth, B.clientWidth),
      Math.max(D.clientHeight, B.clientHeight)];
  }
}



