
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import OrderWhole from './_OrderWhole.vue'
import PendingPayment from './_PendingPayment.vue'

export default {
name: 'order',
components: {
Tab, TabItem, Sticky,OrderWhole,PendingPayment
},
data() {
return {
activeIndex:0
}
},
methods: {
onItemClick(index) {
this.activeIndex = index
}
}
}



