
import Group from 'vux/src/components/group/index.vue'

import MeCell from '../../componets/common/MeCell.vue'
import Scroll from '../../componets/common/Scroll/index'
import {timetrans} from '../../config/functions/index'

export default {
name: 'financialFlow',
components: {
Group, MeCell, Scroll
},
data() {
return {
recordList: [],
pullDown: true,
pageHeight: 0,
pageCurrent: 1,
pageSize: 15,
pageCount: 0
}
},
methods: {
userRechargeRecord() {
this.$httpGet('/wx/user/userFinacialFlow', {
pageCurrent: this.pageCurrent,
pageSize: this.pageSize
}).then((data) => {
if (this.pageCurrent == 1) {
data.data.list.forEach(function (item) {
item.createTime = timetrans(item.createTime)
})
this.recordList = data.data.list
} else {
let _this = this
data.data.list.forEach(function (item) {
item.createTime = timetrans(item.createTime)
_this.recordList.push(item)
})
}
this.pageCurrent = data.data.pageCurrent

this.pageCount = Math.ceil(data.data.total / data.data.pageSize)
}).catch(err => {
this.$vux.toast.text('系统出错', 'middle')
})
},
moreLoad() {
if (this.pageCurrent < this.pageCount) {
this.pageCurrent += 1
this.userRechargeRecord()
}
},
toDetails(obj){
this.$router.push({name:'RechargeRecordDetails'})
this.$store.commit('setRecord',obj)
}
},
created() {
this.userRechargeRecord()
this.$nextTick(function () {
this.pageHeight = document.getElementById("vux_view_box_body").clientHeight - 46
})
}
}



