
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
name: 'addressItem',
components:{
CheckIcon
},
props:{
addressData:{
type:Object,
default:function () {
return {
id:'',
province:"",
city:"",
area:" ",
address:"",
mobile:"",
name:"",
areaCode:"",
cityCode:"",
provinceCode:""
}
}
},
selected:{
type:Boolean,
default:false
}
},
data() {
return {

}
},
computed: {
defaultAddress: {
get:function(){
return this.selected
},
set:function(val){
if(val==true){
this.$emit('changeDefault',this.addressData.id)
}
}
}
},
methods:{
toEdit(){
this.$store.dispatch('transmitAddress',this.addressData)
this.$router.push({name:'editAddress'})
},
deleteItem(){
this.$httpPost('/wx/userAddress/delete',{
id:this.addressData.id
}).then((data)=>{

this.$vux.toast.text(data.data,'middle')
this.$emit('reload')
}).catch(err=>{
this.$vux.toast.text(err.message,'middle')
})
}
}
}



