
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import Group from 'vux/src/components/group/index.vue'


export default {
name: 'goodsInCart',
components: {
SwipeoutItem, SwipeoutButton, CheckIcon, XNumber,InlineXNumber , Group
},
props: {
goodsData: {
type: Object,
default: function () {
return {
id: '',
img: '',
name: '',
num: 1,
price: 0,
checkFlag: false
}
}
}
},
data() {
return {
sonData: this.goodsData,
roundValue: this.goodsData.num
}
},
watch: {
'goodsData':{
deep: true,
handler(val, oldVal) {

this.$httpPost('/wx/order/updateShopCartNum',{
id:val.id,
num:val.num
}).then((data)=>{
console.log(data)
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
}
}
},
computed: {
totalPrice: function () {
return this.goodsData.num * this.goodsData.price
}
},
methods: {
deleteItem() {
let _this = this
this.$vux.confirm.show({
title: '提示',
content: '是否确认删除？',
onConfirm() {
_this.$emit('deleteItem', _this.goodsData.id)
_this.$httpPost('/wx/order/delShopCart', {
id: _this.goodsData.id
}).then((data) => {
_this.$vux.toast.text('删除成功','middle')
}).catch(err => {
_this.$vux.toast.text('系统出错', 'middle')
})
}
})
}
}
}



