
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

export default {
name: 'login',
components:{
Tab, TabItem,Countdown
},
data() {
return {
activeIndex:0,
startCountDown:false,
time:5,
start:false,
nickName:'gcx',
pwd:'123456'
}
},
methods:{
handler(index){
this.activeIndex = index
},
finish(){
this.startCountDown = false
this.time = 5
this.start = false
},
getCode(){
this.startCountDown = true
this.start = true
},
login(){
this.$httpPost('/wx/public/login',{
nickName:this.nickName,
pwd:this.pwd
}).then((data)=>{
this.$store.commit('setToken',{token:data.data})
this.$httpGet('/wx/user/userDetail',{
}).then((data)=>{
this.$store.dispatch('signIn', {userInfo: data.data, rememberMe: false});
this.$router.push({name:'home'})
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
}
}
}



