
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
name: 'addAddress',
components: {
XInput, Group, PopupPicker
},
data() {
return {
id: this.$store.getters.addressInfo.id,
name: this.$store.getters.addressInfo.name,
mobile: this.$store.getters.addressInfo.mobile,
provinceCode: this.$store.getters.addressInfo.provinceCode,
cityCode: this.$store.getters.addressInfo.cityCode,
areaCode: this.$store.getters.addressInfo.areaCode,
address: this.$store.getters.addressInfo.address,
comAddress: [this.$store.getters.addressInfo.provinceCode, this.$store.getters.addressInfo.cityCode, this.$store.getters.addressInfo.areaCode],
addressData: [[''], [''], ['']],
selectedSheng: '',
selectedShi: '',
selectedQu: ''
}
},
methods: {
save() {
this.$store.dispatch('headerSave', {
url: '/wx/userAddress/update',
params: {
id: this.id,
name: this.name,
mobile: this.mobile,
provinceCode: this.comAddress[0],
cityCode: this.comAddress[1],
areaCode: this.comAddress[2],
address: this.address
}
})
},
onChange(ids, names) {
this.selectedSheng = ids[0]
this.selectedShi = ids[1]
this.selectedQu = ids[2]
},
findByProvince() {
this.$httpPost('/wx/adminiStrativeAddress/findByProvince', {}).catch(err => {
let data = []
err.forEach(function (item) {
data.push({
name: item.name,
value: item.provinceCode
})
})
this.$set(this.addressData, 0, data)
})
},
findByProvinceCodeAndType(provinceCode) {
this.$httpPost('/wx/adminiStrativeAddress/findByProvinceCodeAndType', {
provinceCode: provinceCode
}).catch(err => {
let data = []
err.forEach(function (item) {
(item[0]) && data.push({
name: item[1],
value: item[0]
})
})
this.$set(this.addressData, 1, data)
})
},
findByCityCodeAndType(cityCode) {
this.$httpPost('/wx/adminiStrativeAddress/findByCityCodeAndType', {
cityCode: cityCode
}).catch(err => {
let data = []
err.forEach(function (item) {
(item[0]) && data.push({
name: item[1],
value: item[0]
})
})
this.$set(this.addressData, 2, data)
})
}
},
created() {
this.findByProvince()
},
computed: {
completed: {
get: function () {
return this.$store.getters.operation
},
set: function (val) {
}
},
resData: {
get: function () {
return this.$store.getters.result
},
set: function (val) {
console.log(val)
}
}
},
watch: {
'completed': function (val) {
(val) && this.save()
},
'resData': function (val) {
console.log(val)
if (val.statusCode == '300') {
this.$vux.toast.text(val.message, 'middle')
} else if (val.statusCode == '200') {
this.$vux.toast.text(val.data, 'middle')
this.$router.go(-1)

}
},
'selectedSheng': function (val) {
this.findByProvinceCodeAndType(val)
},
'selectedShi': function (val) {
this.findByCityCodeAndType(val)
}
}
}



