
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

export default {
name: 'mePopupPicker',
components:{
PopupPicker
},
props:{
title:{
type:String,
default:''
},
data:{
type:Array,
default:function () {
return []
}
},
choosedItem:{
type:Array,
default:function () {
return []
}
}
},
data() {
return {

list: this.data
}
},
computed:{
'choosed':{
get: function () {
return this.choosedItem
},
set: function (v) {
this.$emit('on-change', v)
}
}
}
}



