const ADDRESS_INFO_PARAM = 'addressInfo';
function storage(bool) {
  return bool ? window.localStorage : window.sessionStorage
}
function getStorageItem (item) {
  return window.localStorage.getItem(item) || window.sessionStorage.getItem(item)
}
export default {
  state: {
    addressInfo:JSON.parse(getStorageItem(ADDRESS_INFO_PARAM))
  },
  getters: {
    addressInfo:state => state.addressInfo
  },
  mutations: {
    setAddressInfo(state,{id,name,mobile,provinceCode,cityCode,areaCode,address,area,city,province}){
      state.addressInfo = {id,name,mobile,provinceCode,cityCode,areaCode,address,area,city,province}
      storage(false).setItem(ADDRESS_INFO_PARAM, JSON.stringify(state.addressInfo))
    }
  },
  actions: {
    transmitAddress:({commit},{id,name,mobile,provinceCode,cityCode,areaCode,address,area,city,province})=>{
      commit('setAddressInfo',{id,name,mobile,provinceCode,cityCode,areaCode,address,area,city,province})
    }
  }
}



