
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import MeCell from '../../componets/common/MeCell.vue'
import MePopupPicker from '../../componets/common/MePopupPicker'

export default {
name: 'goodsDetails',
components: {
Swiper, SwiperItem, Group, MeCell,Badge,MePopupPicker
},
data() {
return {
imgList: [],
goodsId: this.$route.params.id,
goodsName:'',
price:0,
details:[],
list: [['小米', 'iPhone', '华为', '情怀', '三星', '其他', '不告诉你'], ['小米1', 'iPhone2', '华为3', '情怀4', '三星5', '其他6', '不告诉你7']],
choosedItem:[],
shopCartNum:0
}
},
methods: {
getGoodsDetails() {
this.$httpPost('/wx/commodity/detail', {
id: this.goodsId
}).then((data) => {

this.imgList = this.goodsImgFormat(data.data.img)
this.goodsName = data.data.name
this.price = data.data.price
this.details = data.data.details?data.data.details:[]
}).catch(err => {
this.$vux.toast.text(err.message, 'middle')
})
},
goodsImgFormat(list){
let imgList = []
imgList.push({
url:'javascript:',
img:list
})
return imgList
},
changeType(val){
this.choosedItem = val
},
addShopCart(){
this.$httpPost('/wx/order/addShopCart',{
id: this.goodsId
}).then((data)=>{

this.$vux.toast.text(data.data,'middle')
this.shopCartList()
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
},
shopCartList(){
this.$httpGet('/wx/order/shopCartList',{
}).then((data)=>{
this.shopCartNum = data.data.length
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
}
},
created() {
this.getGoodsDetails()
this.shopCartList()
}
}



