const RECORD_PARAM = 'record';
function storage(bool) {
  return bool ? window.localStorage : window.sessionStorage
}
function getStorageItem (item) {
  return window.localStorage.getItem(item) || window.sessionStorage.getItem(item)
}
export default {
  state: {
    record:JSON.parse(getStorageItem(RECORD_PARAM))
  },
  getters: {
    record:state => state.record
  },
  mutations: {
    setRecord(state,{body,createTime,id,money,no,payInfo,payNo,payTime,status,storeInforId,title,type}){
      state.record = {body,createTime,id,money,no,payInfo,payNo,payTime,status,storeInforId,title,type}
      storage(false).setItem(RECORD_PARAM, JSON.stringify(state.record))
    }
  },
  actions: {
    // transmitAddress:({commit},{body,createTime,id,money,no,payInfo,payNo,payTime,status,storeInforId,title,type})=>{
    //   commit('setRechargeRecord',{body,createTime,id,money,no,payInfo,payNo,payTime,status,storeInforId,title,type})
    // }
  }
}



