import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home/Home.vue'
import GoodsDetails from '../pages/goods/GoodsDetails.vue'
import HomePage from '../pages/user-center/HomePage.vue'
import MyData from '../pages/user-center/MyData.vue'
import UserInfo from '../pages/user-center/UserInfo.vue'
import NameIdentify from '../pages/user-center/NameIdentify.vue'
import MyPurse from '../pages/user-center/MyPurse.vue'
import RechargeRecord from '../pages/record/RechargeRecord.vue'
import RechargeRecordDetails from '../pages/record/RechargeRecordDetails.vue'
import ShoppingRecord from '../pages/record/ShoppingRecord.vue'
import ShoppingRecordDetails from '../pages/record/ShoppingRecordDetails.vue'
import FinancialFlow from '../pages/record/FinancialFlow.vue'
import ShoppingCart from '../pages/shopping-cart/ShoppingCart.vue'
import Order from '../pages/order/Order.vue'
import Login from '../pages/login/Login.vue'
import Address from '../pages/address/Address.vue'
import AddAddress from '../pages/address/AddAddress.vue'
import EditAddress from '../pages/address/EditAddress.vue'

Vue.use(Router)

const routes = [
  {path: '/', name: 'home', component: Home, meta: { keepAlive:true }},
  {path: '/login', name: 'login', component: Login },
  {path: '/goodsDetails/:id', name: 'GoodsDetails', component: GoodsDetails ,meta:{title:'商品详情'}},
  {path: '/homePage', name: 'HomePage', component: HomePage, meta: { keepAlive:true }},
  {path: '/homePage/myData', name: 'MyData', component: MyData, meta: {title:'我的资料'}},
  {path: '/homePage/myData/userInfo', name: 'UserInfo', component: UserInfo, meta: {title:'个人信息'}},
  {path: '/homePage/myData/userInfo/nameIdentify', name: 'NameIdentify', component: NameIdentify, meta: {title:'实名认证信息'}},
  {path: '/homePage/myData/userInfo/myPurse', name: 'MyPurse', component: MyPurse, meta: {title:'我的钱包'}},
  {path: '/homePage/myData/record/rechargeRecord', name: 'RechargeRecord', component: RechargeRecord, meta: {title:'充值记录'}},
  {path: '/homePage/myData/record/rechargeRecordDetails', name: 'RechargeRecordDetails', component: RechargeRecordDetails, meta: {title:'充值记录'}},
  {path: '/homePage/myData/record/shoppingRecord', name: 'ShoppingRecord', component: ShoppingRecord, meta: {title:'消费记录'}},
  {path: '/homePage/myData/record/shoppingRecordDetails', name: 'ShoppingRecordDetails', component: ShoppingRecordDetails, meta: {title:'消费记录'}},
  {path: '/homePage/myData/record/financialFlow', name: 'FinancialFlow', component: FinancialFlow, meta: {title:'财务流水'}},
  {path: '/homePage/order', name: 'order', component: Order, meta: {title:'我的订单'}},
  {path: '/shoppingCart', name: 'ShoppingCart', component: ShoppingCart, meta: {title:'购物车',noBack:true,isBlue:true,keepAlive:true}},
  {path: '/homePage/address',name:'address',component:Address,meta:{title:'地址管理'}},
  {path: '/homePage/addAddress',name:'addAddress',component:AddAddress,meta:{title:'添加收货地址',right:'保存'}},
  {path: '/homePage/editAddress',name:'editAddress',component:EditAddress,meta:{title:'修改收货地址',right:'保存'}}
]

let router = new Router({
  routes,
  saveScrollPosition:true
})

export default router



