
import SearchBar from './_SearchBar.vue'
import Swiper from './_Swiper.vue'
import Goods from './_goods.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
name: 'home',
components: {
SearchBar,
Swiper,
Goods,
Group,
Flexbox,
FlexboxItem
},
data() {
return {
linkList: [{
linkName: 'Home',
icon: '/static/images/home_fenxiangbao.png',
label: '分享宝'
}, {
linkName: 'Home',
icon: '/static/images/home_baoleba.png',
label: '报了吧'
}, {
linkName: 'Home',
icon: '/static/images/home_changlegou.png',
label: '厂乐购'
}, {
linkName: 'Home',
icon: '/static/images/home_register.png',
label: '注册'
}],
goodsList: []
}
},
methods: {
getGoodsList() {
this.$httpPost('/wx/commodity/list', {}).then((data) => {
this.goodsList = data.data.list
}).catch(err => {
this.$vux.toast.text(err.message, 'middle')
})
}
},
created: function () {
this.getGoodsList()
},
mounted: function () {
},
activated: function () {

},
deactivated: function () {
}
}



