
import ViewBox from 'vux/src/components/view-box/index.vue'

import AppHeader from './Header.vue'
import AppNav from './Nav.vue'

export default {
name: 'layout',
components: {
ViewBox,
AppHeader,
AppNav
},
data() {
return {}
},
computed: {
isHideHeader() {
return this.$route.name == 'home' || this.$route.name == 'HomePage'||this.$route.name == 'login'
},
isShowNav() {
return this.$route.name == 'home' || this.$route.name == 'HomePage' || this.$route.name == 'ShoppingCart'
},
bodyPaddingTop() {
return this.isHideHeader ? '0px' : '46px'
},
bodyPaddingBottom() {
return this.isShowNav ? '55px' : '0px'
}
}
}



