
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import GoodsInCart from './_GoodsInCart.vue'
import numeral from 'numeral'

export default {
name: 'shoppingCart',
components: {
Swipeout, CheckIcon, XButton, GoodsInCart
},
data() {
return {
cartList: [],
checkAll: false,

totalPrice: 0
}
},
methods: {
deleteItem(val) {
for (let item of this.cartList) {
if (item.id === val) {
this.cartList.splice(item.index, 1)
}
}
},
shopCartList(){
this.$httpGet('/wx/order/shopCartList',{
}).then((data)=>{
console.log(data)
data.data.forEach(function (item) {
item.checkFlag = false
})
this.cartList = data.data
}).catch(err=>{
this.$vux.toast.text('系统出错','middle')
})
}
},
activated(){
this.shopCartList()
},
computed: {

selectAll: {
get: function () {
return this.selectCount == this.cartList.length && this.cartList.length !== 0;
return false;
},
set: function (value) {
this.cartList.forEach(function (item) {
item.checkFlag = value;
});
return value;
}
},

selectCount: {
get: function () {
var i = 0;
this.cartList.forEach(function (item) {
if (item.checkFlag) {
i++;
}
});
return i;
}
},

checkedGroups: {
get: function () {
var checkedGroups = [];
this.cartList.forEach(function (item) {
if (item.checkFlag) {
checkedGroups.push(item);
}
});
return checkedGroups;
}
}
},
watch: {
'cartList': {
deep: true,
handler(val, oldVal) {
let total = 0
val.forEach(item => {
item.checkFlag && (total += item.num * item.price)
})
this.totalPrice = total
}
}
}
}



