export default {
  state: {
    operation:false,
    url:'',
    params:{},
    result:{}
  },
  getters: {
    operation: state => state.operation,
    url: state => state.url,
    params: state => state.params,
    result: state => state.result
  },
  mutations: {
    setOperation(state,{operation}){
      state.operation = operation
    },
    setUrl(state,{url}){
      state.url = url
    },
    setParams(state,{params}){
      state.params = params
    },
    setResult(state,{result}){
      state.result = result
    }
  },
  actions: {
    headerSave:({commit},{url,params})=>{
      commit('setUrl',{url})
      commit('setParams',{params})
    },
    headerRes:({commit},{result})=>{
      commit('setResult',{result})
    }
  }
}



