
import Group from 'vux/src/components/group/index.vue'

import MeCellCard from '../../componets/common/MeCellCard'
import MeCell from '../../componets/common/MeCell.vue'

export default {
name: 'myPurse',
components: {
Group, MeCellCard, MeCell
},
data() {
return {
walletList:[]
}
},
methods: {
userWallet() {
this.$httpGet('/wx/user/userWallet',{})
.then((data) => {

this.walletList = data.data
}).catch(err => {
this.$vux.toast.text('系统错误', 'middle')
})
}
},
created(){
this.userWallet()
}
}



